import axios from "axios";

const isDevelopment = process.env.NODE_ENV === "development" ? true : false;
const endPointApiDev = process.env.REACT_APP_DEV_ENDPOINT;
const protocol = window.location.protocol; //  (http:  https:)
const host = window.location.host; //  host ( localhost:5173)

const apiUrl = isDevelopment ? `${endPointApiDev}` : `${protocol}//${host}`;

//  instance  axios
const axiosInstance = axios.create({
  baseURL: apiUrl,
});

//  Interceptor  Requests
axiosInstance.interceptors.request.use(
  (config) => {
    // //  request    token  headers
    // const token = localStorage.getItem("your-token-storage-key"); //  token
    // if (token) {
    //   config.headers.Authorization = `Bearer ${token}`;
    // }
    return config;
  },
  (error) => {
    //  request error 
    return Promise.reject(error);
  }
);

//  Interceptor  Responses
axiosInstance.interceptors.response.use(
  (response) => {
    //  response data
    return response;
  },
  (error) => {
    // //  response error ,   session 
    // if (error.response && error.response.status === 401) {
    //   // : redirect  login  refresh token
    // }
    return Promise.reject(error);
  }
);

export default axiosInstance;
