import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";

const isDevelopment = process.env.NODE_ENV === "development" ? true : false;
//  querySelectorAll  getElementById  elements  attribute 'data-carousel-id'
const rootElements = document.querySelectorAll(
  isDevelopment ? "[id^='root']" : "[id^='dynamic-showcase-']"
);

//  rootElement  render React app 
if (rootElements) {
  rootElements.forEach((rootElement) => {
    const carouselId = rootElement.getAttribute("data-carousel-id") || "";
    const productIds = rootElement.getAttribute("data-product-ids") || "";
    ReactDOM.createRoot(rootElement).render(
      <App carouselId={carouselId} productIds={productIds} />
    );
  });
}
